import * as vscode from 'vscode';
import { AuthProviderType } from '../github';

export class Log {
	private a: vscode.LogOutputChannel;

	constructor(private readonly b: AuthProviderType) {
		const friendlyName = this.b === AuthProviderType.github ? 'GitHub' : 'GitHub Enterprise';
		this.a = vscode.window.createOutputChannel(`${friendlyName} Authentication`, { log: true });
	}

	public trace(message: string): void {
		this.a.trace(message);
	}

	public info(message: string): void {
		this.a.info(message);
	}

	public error(message: string): void {
		this.a.error(message);
	}

	public warn(message: string): void {
		this.a.warn(message);
	}
}
