import * as vscode from 'vscode';
import TelemetryReporter from '@vscode/extension-telemetry';
import { getExperimentationService, IExperimentationService, IExperimentationTelemetry, TargetPopulation } from 'vscode-tas-client';

export class ExperimentationTelemetry implements IExperimentationTelemetry {
	private a: Record<string, string> = {};
	private b: Promise<IExperimentationService> | undefined;

	constructor(private readonly c: vscode.ExtensionContext, private d: TelemetryReporter) { }

	private async e(): Promise<IExperimentationService> {
		let targetPopulation: TargetPopulation;
		switch (vscode.env.uriScheme) {
			case 'vscode':
				targetPopulation = TargetPopulation.Public;
				break;
			case 'vscode-insiders':
				targetPopulation = TargetPopulation.Insiders;
				break;
			case 'vscode-exploration':
				targetPopulation = TargetPopulation.Internal;
				break;
			case 'code-oss':
				targetPopulation = TargetPopulation.Team;
				break;
			default:
				targetPopulation = TargetPopulation.Public;
				break;
		}

		const id = this.c.extension.id;
		const version = this.c.extension.packageJSON.version;
		const experimentationService = getExperimentationService(id, version, targetPopulation, this, this.c.globalState);
		await experimentationService.initialFetch;
		return experimentationService;
	}

	/**
	 * @returns A promise that you shouldn't need to await because this is just telemetry.
	 */
	async sendTelemetryEvent(eventName: string, properties?: Record<string, string>, measurements?: Record<string, number>) {
		if (!this.b) {
			this.b = this.e();
		}
		await this.b;

		this.d.sendTelemetryEvent(
			eventName,
			{
				...this.a,
				...properties,
			},
			measurements,
		);
	}

	/**
	 * @returns A promise that you shouldn't need to await because this is just telemetry.
	 */
	async sendTelemetryErrorEvent(
		eventName: string,
		properties?: Record<string, string>,
		_measurements?: Record<string, number>
	) {
		if (!this.b) {
			this.b = this.e();
		}
		await this.b;

		this.d.sendTelemetryErrorEvent(eventName, {
			...this.a,
			...properties,
		});
	}

	setSharedProperty(name: string, value: string): void {
		this.a[name] = value;
	}

	postEvent(eventName: string, props: Map<string, string>): void {
		const event: Record<string, string> = {};
		for (const [key, value] of props) {
			event[key] = value;
		}
		this.sendTelemetryEvent(eventName, event);
	}

	dispose(): Promise<any> {
		return this.d.dispose();
	}
}
