import * as vscode from 'vscode';
import { Log } from './logger';

export class Keychain {
	constructor(
		private readonly a: vscode.ExtensionContext,
		private readonly b: string,
		private readonly c: Log
	) { }

	async setToken(token: string): Promise<void> {
		try {
			return await this.a.secrets.store(this.b, token);
		} catch (e) {
			// Ignore
			this.c.error(`Setting token failed: ${e}`);
		}
	}

	async getToken(): Promise<string | null | undefined> {
		try {
			const secret = await this.a.secrets.get(this.b);
			if (secret && secret !== '[]') {
				this.c.trace('Token acquired from secret storage.');
			}
			return secret;
		} catch (e) {
			// Ignore
			this.c.error(`Getting token failed: ${e}`);
			return Promise.resolve(undefined);
		}
	}

	async deleteToken(): Promise<void> {
		try {
			return await this.a.secrets.delete(this.b);
		} catch (e) {
			// Ignore
			this.c.error(`Deleting token failed: ${e}`);
			return Promise.resolve(undefined);
		}
	}
}
